import axios from 'axios'
import Cookie from 'js-cookie'

const instance = axios.create({
    baseURL: 'https://conduit.productionready.io'
})

if (Cookie.get('auth')) {
    var unInterceptor = instance.interceptors.request.use((config) => {
        config.headers.Authorization = 'Token ' + Cookie.getJSON('auth').auth
        return config;
    }, (error) => {
        return Promise.reject(error);
    });
}
export {
    instance,
    unInterceptor
}