import React, { Component } from 'react'
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom'
import Cookie from 'js-cookie'
import PubSub from 'pubsub-js'
import './App.css'
import Header from './Layout/Header'
import Footer from './Layout/Footer'
import Home from './Pages/Home'
import Login from './Pages/Login'
import Editor from './Pages/Editor'
import Settings from './Pages/Settings'
import Profile from './Pages/Profile'
import Article from './Pages/Article'
export default class App extends Component {
  state = {
    username: Cookie.getJSON('auth') && Cookie.getJSON('auth').username
  }
  componentDidMount() {
    if (!this.state.username) {
      this.token = PubSub.subscribe('auth', (msg, data) => {
        const { username } = data
        this.setState({ username })
      })
      Cookie.getJSON('auth') && this.setState({ username: Cookie.getJSON('auth').username })
    }

  }
  componentWillUnmount() {
    PubSub.unsubscribe(this.token)
  }
  render() {
    const { username } = this.state
    return (
      <Router>
        <div>
          <Header />
          <Switch>
            <Route exact path="/" component={Home} />
            {Cookie.get('auth') ? '' : <Route path="/login" component={Login} />}
            {Cookie.get('auth') ? '' : <Route path="/register" component={Login} />}
            <Route path="/editor" component={Editor} />
            <Route path="/settings" component={Settings} />
            <Route path={`/@${username}`} component={Profile} />
            <Route path="/article" component={Article} />
            <Redirect to="/" />
          </Switch>
          <Footer />
          <a className="ForkOnGitHub" href="https://github.com/gothinkster/angularjs-realworld-example-app" target="_blank" rel="noreferrer">
            <i className="ion-social-github"></i>
            &nbsp;&nbsp;Fork on GitHub
          </a>
        </div >
      </Router>
    )
  }
}
