import React, { Component } from 'react'
import Cookie from 'js-cookie'
import { instance } from '../../axios'
export default class Settings extends Component {
    state = {
        title: 'Settings — Conduit',
        username: '',
        email: '',
        password: '',
        image: '',
        bio: ''
    }
    changed = (k) => {
        return (e) => {
            const { target: { value } } = e
            this.setState({ [k]: value })
        }
    }
    onSubmit = (e) => {
        e.preventDefault()
        const { username, email, image, bio } = this.state
        instance({
            method: 'put',
            url: '/api/user',
            data: {
                user: {
                    email,
                    image,
                    bio,
                }
            }
        }).then(res => {
            if (res.status !== 200) return
            this.props.history.push(`/@${username}`)
        })
    }
    logOut = () => {
        Cookie.remove('auth')
        this.props.history.push('/')
        window.location.reload()
    }
    componentDidMount() {
        const { username, email, image, bio } = Cookie.getJSON('auth')
        this.setState({ username, email, image, bio })
    }
    render() {
        const { username, email, password, image, bio, title } = this.state
        document.title = title
        return (
            <div className="settings-page">
                <div className="container page">
                    <div className="row">
                        <div className="col-md-6 offset-md-3 col-xs-12">
                            <h1 className="text-xs-center">Your Settings</h1>
                            <form onSubmit={this.onSubmit}>
                                <fieldset>
                                    <fieldset className="form-group">
                                        <input className="form-control" type="text" placeholder="URL of profile picture" value={image} onChange={this.changed('image')} />
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <input className="form-control form-control-lg" type="text" placeholder="Your Name" value={username} onChange={this.changed('username')} />
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <textarea className="form-control form-control-lg" rows="8" placeholder="Short bio about you" value={bio} onChange={this.changed('bio')}></textarea>
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <input className="form-control form-control-lg" type="text" placeholder="Email" value={email} onChange={this.changed('email')} />
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <input className="form-control form-control-lg" type="password" placeholder="Password" value={password} onChange={this.changed('password')} />
                                    </fieldset>
                                    <button className="btn btn-lg btn-primary pull-xs-right">
                                        Update Settings
                                    </button>
                                </fieldset>
                            </form>
                            <hr />
                            <button className="btn btn-outline-danger" onClick={this.logOut}>
                                Or click here to logout.
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
