import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import Cookie from 'js-cookie'
import PubSub from 'pubsub-js'
export default class Header extends Component {
    state = {
        auth: '',
        username: '',
        image: ''
    }
    componentDidMount() {
        this.token = PubSub.subscribe('auth', (msg, data) => {
            const { auth, username, image } = data
            this.setState({ auth, username, image })
        })
        if (Cookie.get('auth')) {
            const { auth, username, image } = Cookie.getJSON('auth')
            this.setState({ auth, username, image })
        }
    }
    componentWillUnmount() {
        PubSub.unsubscribe(this.token)
    }
    render() {
        if (Cookie.get('auth')) {
            const { username, image } = this.state
            return (
                <div>
                    <nav className="navbar navbar-light">
                        <div className="container">
                            <a className="navbar-brand" href="/">conduit</a>
                            <ul className="nav navbar-nav pull-xs-right">
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" exact to="/" >
                                        Home
                                </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" to="/editor" >
                                        <i className="ion-compose"></i>&nbsp;New Article
                                </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" to="/settings" >
                                        <i className="ion-gear-a"></i>&nbsp;Settings
                                </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" to={{ pathname: `/@${username}` }} >
                                        <img className="user-pic" src={image} alt="" />
                                        {username}
                                    </NavLink>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            )
        } else {
            return (
                <div>
                    <nav className="navbar navbar-light">
                        <div className="container">
                            <a className="navbar-brand" href="/">conduit</a>
                            <ul className="nav navbar-nav pull-xs-right">
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" exact to="/" >
                                        Home
                                </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" to="/login">
                                        Sign in
                                </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName="active" className="nav-link" to="/register" >
                                        Sign up
                                </NavLink>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            )
        }

    }
}
