import React, { Component } from 'react'
import Cookie from 'js-cookie'
import PubSub from 'pubsub-js'
import { instance } from '../../axios'
export default class Login extends Component {
    state = {
        titleIn: 'Sign in — Conduit',
        titleUp: 'Sign up — Conduit',
        username: '',
        email: '',
        password: ''
    }
    changed = (k) => {
        return (e) => {
            const { target: { value } } = e
            this.setState({ [k]: value })
        }
    }
    isLogin = () => {
        const { match: { path } } = this.props
        return path === '/login'
    }
    onSubmit = (e) => {
        e.preventDefault()
        const { username, email, password } = this.state
        let user = {
            email,
            password
        }
        !this.isLogin() && (user.username = username)
        instance({
            method: 'post',
            url: this.isLogin() ? '/api/users/login' : '/api/users',
            data: {
                user
            }

        }).then(res => {
            if (res.status !== 200) return
            const { data: { user: { token, username, image, bio } } } = res
            Cookie.set('auth', { auth: token, username, image, email, bio })
            PubSub.publish('auth', { auth: token, username, image, email, bio })
            this.props.history.push('/')
            window.location.reload()
        })
    }
    render() {
        const { username, email, password, titleIn, titleUp } = this.state
        const title = this.isLogin ? titleIn : titleUp
        document.title = title
        return (
            <div className="auth-page">
                <div className="container page">
                    <div className="row">
                        <div className="col-md-6 offset-md-3 col-xs-12">
                            <h1 className="text-xs-center">
                                {this.isLogin() ? 'Sign in' : 'Sign up'}
                            </h1>
                            <p className="text-xs-center">
                                <a href="/">
                                    {this.isLogin() ? 'Need an account?' : 'Have an account?'}
                                </a>
                            </p>
                            <ul className="error-messages">
                                <li>That email is already taken</li>
                            </ul>
                            <form onSubmit={this.onSubmit}>
                                <fieldset className="form-group" style={{ display: this.isLogin() ? 'none' : 'block' }}>
                                    <input className="form-control form-control-lg" type="text" placeholder="Your Name" value={username} onChange={this.changed('username')} />
                                </fieldset>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="text" placeholder="Email" value={email} onChange={this.changed('email')} />
                                </fieldset>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" type="password" placeholder="Password" value={password} onChange={this.changed('password')} />
                                </fieldset>
                                <button className="btn btn-lg btn-primary pull-xs-right">
                                    {this.isLogin() ? 'Sign in' : 'Sign up'}
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
