import React, { Component } from 'react'
import Cookie from 'js-cookie'
import { instance, unInterceptor } from '../../axios'
import { addFavorite, deleteFavorite } from '../../axios/favorite'
export default class Home extends Component {
    state = {
        title: 'Home — Conduit',
        articles: [],
        tags: [],
        rows: 20,
        articlesCount: 0,
        currentPage: 1,
        favoriteFlag: false
    }
    pageClick = (currentPage) => {
        return (e) => {
            e.preventDefault()
            const { rows } = this.state
            instance({
                method: 'get',
                url: '/api/articles',
                params: {
                    limit: rows,
                    offset: (currentPage - 1) * rows
                }
            }).then((res) => {
                if (res.status !== 200) return
                const { data: { articles } } = res
                this.setState({ articles, currentPage })
            })
        }
    }
    favoriteClick = (data, i) => {
        return async () => {
            const { articles, favoriteFlag } = this.state
            this.setState({ favoriteFlag: !favoriteFlag })
            if (!Cookie.get('auth')) {
                this.props.history.push('/login')
                return
            }
            if (data.favorited) {
                this.deleteFavorite = await deleteFavorite(data).then(res => {
                    if (res.status !== 200) return
                    const { data: { article } } = res
                    const newArticles = articles.map((item, index) => index === i ? article : item)
                    const { favoriteFlag } = this.state
                    this.setState({ articles: newArticles, favoriteFlag: !favoriteFlag })
                })
            } else {
                this.addFavorite = await addFavorite(data).then(res => {
                    if (res.status !== 200) return
                    const { data: { article } } = res
                    const newArticles = articles.map((item, index) => index === i ? article : item)
                    const { favoriteFlag } = this.state
                    this.setState({ articles: newArticles, favoriteFlag: !favoriteFlag })
                })
            }
        }
    }
    componentDidMount() {
        const { rows } = this.state
        instance({
            method: 'get',
            url: '/api/articles',
            params: {
                limit: rows,
                offset: 0
            }
        }).then((res) => {
            if (res.status !== 200) return
            const { data: { articles, articlesCount } } = res
            this.setState({ articles, articlesCount })
        })

        instance({
            method: 'get',
            url: '/api/tags',
        }).then((res) => {
            if (res.status !== 200) return
            const { data: { tags } } = res
            this.setState({ tags })
        })
    }
    componentWillUnmount() {
        instance.interceptors.request.eject(unInterceptor)
        this.setState = (state, callback) => { return }
    }
    render() {
        const { articles, title, articlesCount, rows, currentPage, tags, favoriteFlag } = this.state
        document.title = title
        return (
            <div className="home-page">
                <div className="banner" style={{ display: Cookie.get('auth') ? 'none' : 'block' }}>
                    <div className="container" >
                        <h1 className="logo-font">conduit</h1>
                        <p>A place to share your knowledge.</p>
                    </div>
                </div>
                <div className="container page">
                    <div className="row">
                        <div className="col-md-9">
                            <div className="feed-toggle">
                                <ul className="nav nav-pills outline-active">
                                    <li className="nav-item" >
                                        <a className="nav-link disabled" href="/">Your Feed</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link active" href="/">Global Feed</a>
                                    </li>
                                </ul>
                            </div>
                            {
                                articles.map((item, i) => {
                                    return (
                                        <div key={i} className="article-preview">
                                            <div className="article-meta">
                                                <a href="profile.html">
                                                    <img src={item.author.image} alt="" />
                                                </a>
                                                <div className="info">
                                                    <a href="/" className="author">{item.author.username}</a>
                                                    <span className="date">{this.$formatDate(item.updatedAt).format('MMM,D,YYYY')}</span>
                                                </div>
                                                <button className={`btn btn-sm pull-xs-right ${item.favorited ? 'btn-primary' : 'btn-outline-primary'} ${favoriteFlag ? 'disabled' : ''}`} onClick={this.favoriteClick(item, i)}>
                                                    <i className="ion-heart"></i> {item.favoritesCount}
                                                </button>
                                            </div>
                                            <a href="/" className="preview-link">
                                                <h1>{item.title}</h1>
                                                <p>{item.description}</p>
                                                <span>Read more...</span>
                                                <ul className="tag-list">
                                                    {
                                                        item.tagList.map((item, i) => {
                                                            return (
                                                                <li key={i} className="tag-default tag-pill tag-outline">
                                                                    {item}
                                                                </li>
                                                            )
                                                        })
                                                    }
                                                </ul>
                                            </a>
                                        </div>
                                    )
                                })
                            }
                            <ul className="pagination">
                                {
                                    [...new Array(articlesCount / rows)].map((item, i) => {
                                        return (
                                            <li key={i} className={['page-item', currentPage === (i + 1) ? 'active' : ''].join(' ')}>
                                                <a className="page-link" href="/" onClick={this.pageClick(i + 1)}>{i + 1}</a>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </div>
                        <div className="col-md-3">
                            <div className="sidebar">
                                <p>Popular Tags</p>
                                <div className="tag-list">
                                    {
                                        tags.map((item, i) => {
                                            return (
                                                <a key={i} href="/" className="tag-pill tag-default">
                                                    {item}
                                                </a>
                                            )
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        )
    }
}
